module.exports = ({ stores, views }) => instanceId => {

    const $tagContainer = document.createElement('div');
    $tagContainer.className = 'tag-container';

    const $tag = document.createElement('div');
    $tagContainer.append($tag);

    $tag.append(views.tags.layout(instanceId));

    stores.tagInstances.id(instanceId).onChange('roleId', ({ tagId, roleId, mode }) => {
        $tag.className = `tag tag${tagId} role${roleId} ${mode}`;

        const role = stores.roles.getState(roleId);
        $tag.classList.toggle('nil-role', !role.name);
    });

    return $tagContainer;

};



