module.exports = ({ constants, stores }) => () => {

    const $navBar = document.createElement('nav');
    $navBar.className = 'nav-bar';

    const $tips = document.createElement('a');
    $tips.className = 'tips';
    $tips.textContent = 'Tips & tricks';

    const $issues = document.createElement('a');
    $issues.textContent = 'Issues, feature requests, feedback';
    $issues.target = '_blank';
    $issues.href = constants.app.issues;

    $navBar.append($tips, $issues);

    $tips.addEventListener('click', () => {
        stores.app.setState({ modal: 'tipsAndTricks' });
    });

    return $navBar;

};
