module.exports = ({ lib, stores }) => () => {

    const $container = document.createElement('div');
    $container.className = 'tag-image-container';

    const $xrayImage = document.createElement('div');
    $xrayImage.className = 'tag-xray-image hide';

    const xrayVisibilityToggle = lib.visibilityToggle($xrayImage);
    stores.options.onChange('xray', ({ xray }) => {
        xrayVisibilityToggle.toggle(xray);
        $container.classList.toggle('xray', xray);
    });

    const $image = document.createElement('div');
    $image.className = 'tag-image';

    $container.append($xrayImage, $image);

    return $container;

};
