module.exports = ({ lib }) => contentElement => {

    const $notice = document.createElement('div');
    $notice.className = 'notice hide';

    const noticeVisibility = lib.visibilityToggle($notice);

    const $dismiss = document.createElement('span');
    $dismiss.className = 'dismiss';
    $dismiss.addEventListener('click', noticeVisibility.hide);

    const $content = document.createElement('div');
    $content.className = 'notice-content';
    $content.append(contentElement);

    const $window = document.createElement('div');
    $window.className = 'notice-window';
    $window.append($dismiss, $content);

    $notice.append($window);

    return $notice;

};
