module.exports = ({ stores, views }) => roleId => {

    const role = stores.roles.getState(roleId);

    const $roleCustomiser = document.createElement('span');
    $roleCustomiser.className = `role-customiser role${roleId}`;
    $roleCustomiser.classList.toggle('nil-role', !role.name);

    const $masterRoleBadge = views.roles.masterRoleBadge(roleId);
    const $colourPicker = views.roles.colourPicker(roleId);

    $roleCustomiser.append($masterRoleBadge, $colourPicker);

    return $roleCustomiser;

};
