module.exports = ({ stores }) => () => {

    const $xray = document.createElement('span');
    $xray.className = 'xray';
    $xray.innerText = 'X-Ray';
    $xray.title = '';

    $xray.addEventListener('click', () => {
        const { xray } = stores.options.getState();
        stores.options.setState({ xray: !xray });
    });

    stores.options.onChange('xray', ({ xray }) => {
        $xray.classList.toggle('selected', xray);
    });

    return $xray;

};
