module.exports = ({ gateways, constants, fetch }) => async (email, fallback) => {

    const { domain, size } = constants.gravatar;
    const emailHash = gateways.gravatar.hashEmail(email);
    const url = `${domain}/avatar/${emailHash}?r=g&s=${size}&d=${fallback}`;
    const response = await fetch(url);
    return response.blob();

};
