module.exports = () => filename => {

    return filename.match(/^(\d+)?(.+)/)[2].split('.')[0].split('+').map(s => s.trim());

};



/*

- Leading numbers are stripped, allowing them to be used for ordering on a local file system.
  This enables the user to drag and drop a selection of files and have them rendered in their preferred order.
- Any file extensions are stripped from the first dot.
- The remainder of the filename is split on + to allow an optional role name.

*/
