module.exports = ({ stores, styles }) => () => {

    const $styles = document.createElement('div');
    $styles.className = 'styles';

    const appendRoleStyle = roleId => $styles.append(styles.role(roleId));

    const appendTagImageStyle = tagId => $styles.append(styles.tagImage(tagId));

    stores.roles.ids().forEach(appendRoleStyle);
    stores.roles.onInsert(appendRoleStyle);

    stores.tags.ids().forEach(appendTagImageStyle);
    stores.tags.onInsert(appendTagImageStyle);

    $styles.append(
        styles.colourPicker(),
        styles.main(),
        styles.tagShape(),
        styles.tagSize(),
        styles.tagSpacing(),
        styles.tagOutline()
    );

    return $styles;

};
