module.exports = ({ stores, components }) => () => {

    const $container = document.createElement('span');

    const $showOutline = document.createElement('input');

    $container.append($showOutline, 'Show outline');

    $showOutline.type = 'checkbox';
    $showOutline.checked = true;

    $showOutline.addEventListener('change', () => {
        stores.options.setState({ outline: $showOutline.checked });
    });

    return $container;

};
