module.exports = ({ stores }) => instanceData => {

    const { tagId, mode } = instanceData;
    const tag = stores.tags.getState(tagId);
    const { name, roleId } = tag;
    const { name: roleName } = stores.roles.getState(roleId);
    const changes = { name, roleId, roleName }

    const callback = instanceId => {
        /*
            As an optimisation, track instances for quick lookup.
            */
        stores.tags.setState(tagId, {
            instances: tag.instances.concat(instanceId),
            [mode]: tag[mode].concat(instanceId)
        });
    }

    const instanceId = stores.tagInstances.insert({ ...instanceData, ...changes }, callback);
    return instanceId;


};
