require('regenerator-runtime/runtime');

const wire = require('./wire');
const constants = require('./constants');

const modules = {
    lib: require('./lib'),
    gateways: require('./gateways'),
    stores: require('./stores'),
    services: require('./services'),
    styles: require('./styles'),
    components: require('./components'),
    views: require('./views')
};

module.exports = (args = {}) => {
    const {
        fetch = window.fetch,
        URL = window.URL,
        random = Math.random
    } = args;

    const wiring = {
        lib: 'random',
        gateways: 'fetch constants',
        stores: 'lib',
        services: 'constants lib gateways stores URL',
        styles: 'lib stores services',
        components: 'lib',
        views: 'constants lib stores services components styles'
    };

    const app = wire(modules, wiring, { constants, URL, random, fetch });

    app.services.state.initialise();

    return app;

};
