module.exports = ({ lib, components }) => () => {

    const $tip = document.createElement('div');
    $tip.innerHTML = `
        Save time by renaming files to include name, and optionally, role (separated by +) e.g. matt+dev.jpg<br>
        Once images are provided, <em>names and roles can be edited directly on each avatar</em>.<br>
        When done: print, laminate, and decorate your agile board!
    `;

    const $notice = components.notice($tip);
    const noticeVisibility = lib.visibilityToggle($notice);
    noticeVisibility.show();
    return $notice;

};
