module.exports = ({ views }) => ({ onSubmit }) => {

    const $gravatar = document.createElement('div');
    $gravatar.title = 'Gravatar';
    $gravatar.className = 'gravatar';

    $gravatar.append(
        views.gravatar.freetext(),
        views.gravatar.fallbacks(),
        views.gravatar.actions({ onSubmit })
    );

    return $gravatar;

};
