module.exports = ({ stores }) => () => {

    return stores.tags.getItems().sort((a, b) => {
        const { name: roleNameA } = stores.roles.getState(a.roleId);
        const { name: roleNameB } = stores.roles.getState(b.roleId);
        const roleComparison = roleNameA.localeCompare(roleNameB);
        const nameComparison = a.name.localeCompare(b.name);
        return roleComparison || nameComparison;
    });

};
