module.exports = ({ constants }) => () => {

    const $titleBar = document.createElement('h1');
    $titleBar.textContent = constants.app.name;

    const $author = document.createElement('a');
    $author.className = 'author';
    $author.href = constants.author.profile
    $author.textContent = `by ${constants.author.name}`;
    $author.target = '_blank';
    
    $titleBar.append($author);

    return $titleBar;

};
