// TODO: Refactor.

module.exports = ({ services }) => () => imageFile => {

    const fileLoaded = (name, roleName) => e => {
        const image = e.target.result;
        const roleId = services.roles.findOrInsertRoleWithName(roleName);
        const tag = { name, roleId, image };
        services.tags.insertTag(tag);
    };

    if (!imageFile.type.startsWith('image/')) return;
    const [name, roleName] = services.tags.parseFilename(imageFile.name);
    const reader = new window.FileReader();
    reader.onload = fileLoaded(name, roleName);

    reader.readAsDataURL(imageFile);

};
