const HIDE = 'hide';
const SHOW = 'show';

module.exports = () => targetElement => {

    if (!targetElement.classList.contains(HIDE)) targetElement.classList.add(SHOW);

    const show = () => targetElement.classList.replace(HIDE, SHOW);
    const hide = () => targetElement.classList.replace(SHOW, HIDE);
    const toggle = b => b ? show() : hide();
    return { show, hide, toggle };

};



/*

- Toggles classes between `hide` and `show`. Actual visibility determined by CSS.
- Original design only toggled `show`. Explicit `hide` was added to improve readability of CSS.
- Used to toggle visibility of `modal` and `notice` components.

*/
