module.exports = ({ stores, services, views }) => () => {

    const roles = {};

    const $roles = document.createElement('div');

    const sort = () => {
        services.roles.sortRoles().forEach(role => {
            const $role = roles[role.id];
            if ($role) $roles.appendChild($roles.removeChild($role));
        });
    };

    const insert = roleId => {
        const $role = views.roles.roleCustomiser(roleId);
        roles[roleId] = $role;
        $roles.appendChild($role);
    };

    const insertThenSort = roleId => { insert(roleId); sort() };

    stores.roles.ids().forEach(insertThenSort);
    stores.roles.onInsert(insertThenSort);
    stores.options.onChange('keepSortedBy', sort);
    stores.tagInstances.onAnyChange({ roleName: sort });

    return $roles;

};
