module.exports = ({ constants, gateways, fetch }) => async email => {

    const { domain } = constants.gravatar;
    const emailHash = gateways.gravatar.hashEmail(email);
    const url = `${domain}/${emailHash}.json`;
    const response = await fetch(url);
    if (!response.ok) return null;
    const data = await response.json();
    return data.entry[0];

};
