module.exports = ({ constants, lib, stores, services, views }) => () => {

    const tagsInstances = {};

    const $tags = document.createElement('div');
    $tags.className = 'tags';

    const insertTag = instanceId => {
        const $tag = views.tags.tag(instanceId);
        tagsInstances[instanceId] = $tag;
        $tags.append($tag);
    };

    const removeTag = instanceId => {
        tagsInstances[instanceId].remove();
        delete tagsInstances[instanceId];
    };

    stores.tagInstances.onInsert(instanceId => {
        insertTag(instanceId);
        delayedSort();
    });

    stores.tagInstances.onBeforeRemove(removeTag);

    const sort = () => {
        services.tags.sortInstances().forEach(instance => {
            $tags.appendChild($tags.removeChild(tagsInstances[instance.id]));
        });
    };

    const delayedSort = lib.debounce(sort, constants.debounce.sortTagList);

    stores.options.onChange('keepSortedBy', sort);

    stores.tagInstances.onAnyChange(lib.sameValue(['name', 'roleName'], delayedSort));

    return $tags;

};
