const BORDER_RADIUS = { square: 0, circle: 50 };

module.exports = ({ stores }) => () => {

    const $style = document.createElement('style');

    stores.options.onChange('shape', ({ shape }) => {
        $style.textContent = `
            .tag-image {
                border-radius: ${BORDER_RADIUS[shape]}%;
            }`;
    });

    return $style;

};
