module.exports = ({ lib, constants, stores, components }) => optionName => {

    const { min, max, step } = constants.options[optionName];

    const $number = document.createElement('input');
    $number.type = 'number';
    $number.className = optionName;
    $number.min = min;
    $number.max = max;
    $number.step = step;

    const onChange = () => {
        const val = parseInt($number.value) || 0;
        const newVal = val < min ? min : (val > max ? max : val);
        $number.value = newVal;
        stores.options.setState({ [optionName]: newVal });
    };

    $number.addEventListener('input', () => {
        if (!$number.value) return;
        onChange();
    });

    $number.addEventListener('blur', () => {
        onChange();
    });

    // stores.options.onChange(optionName, () => $number.value = stores.options.getState()[optionName]);

    stores.options.onChange(optionName, state => $number.value = state[optionName]); // issue on first run

    const labelText = lib.upperFirst(optionName);
    return components.label(labelText, $number);

};
