module.exports = ({ stores }) => () => {

    const $style = document.createElement('style');

    stores.options.onChange('outline', ({ outline }) => {

        const border = outline ? 'dashed 1px silver' : 'dashed 1px transparent';

        $style.textContent = `
            .tag {
                border: ${border};
            }`;
    });

    return $style;

};
