module.exports = ({ stores }) => tagId => {

    const { image } = stores.tags.getState(tagId);

    const $style = document.createElement('style');
    $style.className = `tag${tagId}`;

    $style.textContent = `
        .tag${tagId} .tag-image, .tag${tagId} .tag-xray-image { 
            background-image: url(${image}); 
        }`;

    return $style;

};
