const status = {
    ready: 'ready',
    working: 'working',
    error: 'error'
};

module.exports = ({ stores }) => {

    const statusIs = status => () => stores.gravatar.getState().status === status;

    const isReady = statusIs(status.ready);
    const isWorking = statusIs(status.working);
    const isError = statusIs(status.error);

    const ready = () => {
        stores.gravatar.setState({ status: status.ready, freetext: '', errorMessage: '' });
    };

    const working = () => {
        stores.gravatar.setState({ status: status.working });
    };

    const error = errorMessage => {
        stores.gravatar.setState({ status: status.error, errorMessage });
    };

    return { isReady, isWorking, isError, ready, working, error };

};
