const Picker = require('vanilla-picker').default || require('vanilla-picker');

module.exports = ({ stores }) => roleId => {

    const $colourPicker = document.createElement('div');
    $colourPicker.className = 'colour-picker';

    const role = stores.roles.getState(roleId);

    new Picker({
        parent: $colourPicker,
        color: role.colour,
        onChange: e => stores.roles.setState(roleId, { colour: e.hex })
    });

    return $colourPicker;

};
