module.exports = ({ components, views }) => onDismiss => {

    const $title = document.createElement('span');
    $title.textContent = 'Import images from Gravatar';

    const $whatIsGravatar = document.createElement('a');
    $whatIsGravatar.text = 'What is Gravatar?';
    $whatIsGravatar.href = 'https://en.gravatar.com/support/what-is-gravatar/'
    $whatIsGravatar.target = '_blank';
    $title.append($whatIsGravatar)

    return components.modal({
        content: views.gravatar.importImages({ onSubmit: onDismiss }),
        title: $title,
        onDismiss
    });

};
