module.exports = ({ gateways }) => async email => {

    const profile = await gateways.gravatar.fetchProfile(email);
    return profile ? extractNameFromProfile(profile) : extractNameFromEmail(email);

};

const extractNameFromProfile = profile => profile.name.givenName || profile.displayName;

const extractNameFromEmail = email => email.split('@')[0];



/*

- Given name is optional in Gravatar.
- Not concerned about the validity of the email address.

*/