module.exports = ({ stores, components }) => () => {

    const $keepSorted = document.createElement('select');

    const $orderAdded = document.createElement('option');
    $orderAdded.value = 'orderAdded';
    $orderAdded.textContent = 'Order added';

    const $roleThenName = document.createElement('option');
    $roleThenName.value = 'roleThenName';
    $roleThenName.textContent = 'Role, then name';

    const $name = document.createElement('option');
    $name.value = 'name';
    $name.textContent = 'Name';

    $keepSorted.append($orderAdded, $roleThenName, $name);

    $keepSorted.addEventListener('change', e => {
        stores.options.setState({ keepSortedBy: $keepSorted.value });
    });

    return components.label('Keep sorted by', $keepSorted);

};
