const ENTER_KEY_CODE = 13;

module.exports = () => onChange => {

    const $span = document.createElement('span');
    $span.setAttribute('contenteditable', true);
    $span.addEventListener('blur', onChange);
    $span.addEventListener('keypress', keypress(onChange))
    return $span;

};

const keypress = callback => e => {

    if (e.keyCode === ENTER_KEY_CODE) {
        callback(e);
        e.preventDefault();
    }

};



/*

- Content editable span preferred over text field for the ability to expand/contract while editing.

*/
