module.exports = ({ lib, constants, stores, services }) => tagData => {

    const changes = {
        name: lib.upperFirst((tagData.name || '').trim()),
        roleId: tagData.roleId || stores.app.getState().nilRoleId,
        instances: [],
        ...lib.sameValue(constants.options.modes, [])
    };

    const tagId = stores.tags.insert({ ...tagData, ...changes }, tagId => {

        stores.tags.id(tagId).onChange('name', services.tags.propagateTagName(tagId));
        stores.tags.id(tagId).onChange('roleId', services.tags.propagateRoleId(tagId));
        services.tags.adjustInstanceCounts();

    });

    return tagId;

};
