module.exports = ({ components, views }) => onDismiss => {

    const { laminating, multipleAvatars, badges } = views.tips;
    const sequence = [laminating, multipleAvatars, badges];

    const $tips = document.createElement('div');
    $tips.className = 'tips';

    sequence.forEach(render => {
        const $tip = render();
        $tip.className = 'tip';
        const $heading = document.createElement('div');
        $heading.className = 'heading';
        $heading.textContent = $tip.title;
        $tip.prepend($heading);
        $tips.append($tip);
    });

    return components.modal({
        content: $tips,
        title: 'Tips & tricks',
        onDismiss
    });

};
