module.exports = ({ lib, stores, services }) => roleData => {

    const changes = {
        name: roleData.name.trim().toUpperCase(),
        colour: roleData.colour || lib.randomColour().hex
    };

    return stores.roles.insert({ ...roleData, ...changes }, roleId => {
        const wireEvents = () => stores.roles.id(roleId).onChange('name', services.roles.propagateRoleName(roleId));
        const cacheNilRole = () => stores.app.setState({ nilRoleId: roleId });
        const { name } = stores.roles.getState(roleId);
        name ? wireEvents() : cacheNilRole();
    });

};
