module.exports = ({ lib, stores, services }) => ({ onSubmit }) => {

    const $actions = document.createElement('div');
    $actions.className = 'actions';

    const $importButton = document.createElement('button');
    $importButton.className = 'import show';
    $importButton.textContent = 'Import';

    $importButton.addEventListener('click', async () => {
        const ok = await services.tags.insertGravatar();
        if (ok) onSubmit();
    });

    const $loadingSpinner = document.createElement('div');
    $loadingSpinner.className = 'lds-dual-ring hide';

    const $errorMessage = document.createElement('div');
    $errorMessage.className = 'error-message hide';

    const $dismissErrorMessage = document.createElement('a');
    $dismissErrorMessage.textContent = 'Dismiss';

    $dismissErrorMessage.addEventListener('click', services.state.gravatar.ready);

    $actions.append($importButton, $loadingSpinner, $errorMessage);

    const visibilityToggles = {
        importButton: lib.visibilityToggle($importButton),
        loadingSpinner: lib.visibilityToggle($loadingSpinner),
        errorMessage: lib.visibilityToggle($errorMessage)
    };


    stores.gravatar.onChange('status', () => {
        visibilityToggles.importButton.toggle(services.state.gravatar.isReady());
        visibilityToggles.loadingSpinner.toggle(services.state.gravatar.isWorking());
        visibilityToggles.errorMessage.toggle(services.state.gravatar.isError());
    });

    stores.gravatar.onChange('errorMessage', () => {
        const { errorMessage } = stores.gravatar.getState();
        $errorMessage.textContent = errorMessage;
        $errorMessage.append(document.createElement('br'));
        $errorMessage.append($dismissErrorMessage)
    })

    return $actions;

};
