module.exports = ({ lib, stores, views }) => () => {

    const $modals = document.createElement('div');
    $modals.className = 'modals';

    const onDismiss = () => {
        stores.app.setState({ modal: null });
    };

    const { modals, toggles } = Object.entries(views.modals).reduce((acc, [key, modal]) => {
        const $modal = modal(onDismiss);
        acc.modals.push($modal);

        const { toggle } = lib.visibilityToggle($modal);
        acc.toggles.push({ key, toggle });

        return acc;
    }, { modals: [], toggles: [] });


    stores.app.onChange('modal', ({ modal }) => {
        toggles.forEach(({ key, toggle }) => toggle(modal === key));
    });

    $modals.append(...modals);

    return $modals;

};