module.exports = (modules, wiring, initial = {}) => {
    return Object.entries(wiring).reduce((acc, [key, resolver]) => {
        const pipeline = getPipeline(acc, key, modules);
        return pipeline.reduce((acc, fun) => fun(acc) || acc, resolver);
    }, initial);
};

const getPipeline = (acc, key, tree) => [
    str => isString(str) ? str.split(/[ ,;]/).map(key => key.trim()) : str,
    arr => isArray(arr) ? deps => arr.reduce((acc, key) => ({ ...acc, [key]: deps[key] }), {}) : arr,
    fun => {
        const product = {};
        const deps = { ...fun(acc), [key]: product };
        const val = tree[key];
        const private = isFunction(val) ? val(deps) : deepMap(val, deps, product);
        const public = removePrivates(private);
        return Object.assign(acc, { [key]: public });
    }
];

const deepMap = (obj, deps, product) => {
    const privatePattern = /^_/;
    return Object.entries(obj).reduce((acc, [key, val]) => {
        const newVal = isFunction(val) ? val(deps) : deepMap(val, deps, {});
        const private = privatePattern.test(key);
        if (private) newVal.private = true;
        const newKey = key.replace(privatePattern, '');
        return Object.assign(acc, { [newKey]: newVal });
    }, product);
};

const removePrivates = obj => {
    return Object.entries(obj).reduce((acc, [key, val]) => {
        if (val.private) return acc;
        const newVal = isFunction(val) ? val : removePrivates(val, {});
        return Object.assign(acc, { [key]: newVal });
    }, {});
};

const isString = val => typeof val === 'string';
const isArray = val => Array.isArray(val);
const isFunction = val => typeof val === 'function';
