module.exports = ({ stores }) => () => {

    const $style = document.createElement('style');

    stores.options.onChange('size', ({ size }) => {

        $style.textContent = `
            .tags {
                grid-template-columns: repeat(auto-fill, ${size}px);
            }

            .tag-image-container {
                width: ${size - 34}px;
                height: ${size - 34}px;
            }`;

    });

    return $style;

};
