module.exports = ({ stores, components }) => roleId => {

    const editableSpan = () => {
        return components.editableSpan(e => {
            const name = e.target.textContent.trim();
            stores.roles.setState(roleId, { name });
        });
    };

    const role = stores.roles.getState(roleId);
    const $roleBadge = role.name ? editableSpan() : document.createElement('span');
    $roleBadge.className = `role-badge role${roleId}`;
    $roleBadge.textContent = role.name;

    return $roleBadge;

};
