const _app = require('./js');

class AgileAvatars extends HTMLElement {

    constructor () {
        super();
        const shadow = this.attachShadow({ mode: 'open' });
        const app = _app();
        const container = app.views.container();
        shadow.append(container);

        // For debug purposes.
        if (!window.agileavatars) window.agileavatars = [];
        window.agileavatars.push(app);
    }

}

customElements.define('agile-avatars', AgileAvatars);
