module.exports = ({ stores, services }) => state => {

    const initialisers = {
        roles: roles => roles.forEach(services.roles.insertRole),
        // tags: tags => tags.forEach(services.tags.insertTag), // not in use
    };

    Object.entries(state).forEach(([key, state]) => {
        const initialise = initialisers[key] || stores[key].initialise;
        initialise(state);
    });

};
