module.exports = ({ gateways, stores, services, URL }) => async () => {

    const { freetext, fallback } = stores.gravatar.getState();

    const emails = freetext.split(/[\r\n,;]+/g).map(s => s.trim()).filter(s => s);

    services.state.gravatar.working();

    const promises = emails.map(async email => {
        const { name, imageBlob } = await gateways.gravatar.fetchNameAndImage(email, fallback);
        const image = URL.createObjectURL(imageBlob);
        services.tags.insertTag({ name, image });
    });

    try {
        await Promise.all(promises);
        services.state.gravatar.ready();
        return true;
    } catch (err) {
        console.error(err);
        const errorMessage = 'An error occurred. Please check your connection and try again.';
        services.state.gravatar.error(errorMessage);
        return false;
    }

};
